import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';

import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class Wear extends StoreModel {
    constructor() {
        super('wear', {
            namePlural: 'wears',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/demand/',
                    params: {},
                },
            },
        });
    }

    @observable wears = [];

    @observable wearsAi = [];

    @action
    generateData() {
        for (let i = 0, l = 50; i < l; i += 1) {
            this.wears.push(
                wutil.multiply(wutil.makeRandomData({ cnt: 335, min: 100, max: 300, startValue: 170, onlyUp: false, inputData: [], diff: 2 }), 0.1)
            );
            this.wearsAi.push(
                wutil.makeRandomData({ cnt: 335, min: 10, max: 30, onlyUp: false, inputData: this.wears[i], diff: 1, maxSkew: 0 })
            );
        }
    }

    @computed
    get wearsAiFiltered() {
        const now = wutil.getEpoch();
        return this.wearsAi.filter(e => e.x > now);
    }
}

const store = new Wear();
export default store;
