import util from 'preact-util';

class WifetUtil {
    static normalizeRange(val, min, max, newMin, newMax) {
        return newMin + (val - min) * (newMax - newMin) / (max - min);
    }

    /**
    * Returns a random number between min (inclusive) and max (exclusive)
    */
    static getRandom(min, max) {
        return Math.random() * (max - min) + min;
}

    /**
    * Returns a random integer between min (inclusive) and max (inclusive).
    * The value is no lower than min (or the next integer greater than min
    * if min isn't an integer) and no greater than max (or the next integer
    * lower than max if max isn't an integer).
    * Using Math.round() will give you a non-uniform distribution!
    */
    static getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    static makeRandomData({ cnt = 200, min, max, startValue, onlyUp = false, inputData = [], diff = 5, maxSkew = 1, rangeMin, rangeMax, cutoff }) {
        const data = [];
        let current = 0;
        let skew = 0;
        const xData = WifetUtil.timeRangeHours(-4, 10);

        for (let i = 0, l = cnt; i < l; i += 1) {
            if (util.isDefined(inputData[i]) && util.isDefined(inputData[i].y)) {
                skew = WifetUtil.getRandomInt(skew - diff, skew + diff);
                current = inputData[i].y + skew;
                if (Math.abs(skew) > maxSkew) { skew -= skew / 2; }
            } else if (startValue && i === 0) {
                current = startValue;
            } else if (util.isUndefined(current)) {
                current = WifetUtil.getRandomInt(min, max);
            } else if (onlyUp) {
                current = WifetUtil.getRandomInt(current, current + diff);
            } else {
                // console.log('current - diff, current + diff', current - diff, current + diff);
                current = WifetUtil.getRandomInt(current - diff, current + diff);
                // console.log('===> ', current);
            }
            if (current < min) {
                current = min;
            }
            if (current > max) {
                current = max;
            }
            if (rangeMin && rangeMax) {
                current = WifetUtil.normalizeRange(current, min, max, rangeMin, rangeMax);
            }
            // console.log('current', current);
            if (cutoff && i > cutoff) {
                data.push({ x: xData[i] || i, y: null })
            } else {
                data.push({ x: xData[i] || i, y: current })
            }
        }
        return data;
    }

    static getDateDiff(days, inputDate) {
        let date;
        if (inputDate) {
            date = new Date(date);
        } else {
            date = new Date();
        }
        return new Date(date.getTime() + (days * 24 * 60 * 60 * 1000));
    }

    static getDateDiffMidnight(days, inputDate) {
        let date;
        if (inputDate) {
            date = new Date(date);
        } else {
            date = new Date();
        }
        date.setHours(0, 0, 0, 0);
        return new Date(date.getTime() + (days * 24 * 60 * 60 * 1000));
    }

    static getDateDiffTime(days, inputDate) {
        return Math.floor(WifetUtil.getDateDiff(days, inputDate).getTime() / 1000);
    }

    static timeRangeHours(startDay, endDay) {
        const startTimeMs = WifetUtil.getDateDiff(startDay).getTime();
        const endTimeMs = WifetUtil.getDateDiff(endDay).getTime();

        const startTime = Math.floor(startTimeMs / 1000);
        const endTime = Math.floor(endTimeMs / 1000);

        // console.log('startTime', startTime);
        // console.log('endTime', endTime);
        const times = [];
        let currentTime = startTime;
        while(currentTime < endTime) {
            times.push(currentTime);
            currentTime += 3600;
        }
        // console.log('times', times);
        return times;
    }

    static timeRangeDays(startDay, endDay) {
        const startTimeMs = WifetUtil.getDateDiffMidnight(startDay).getTime();
        const endTimeMs = WifetUtil.getDateDiffMidnight(endDay).getTime();

        const startTime = Math.floor(startTimeMs / 1000);
        const endTime = Math.floor(endTimeMs / 1000);

        // console.log('startTime', startTime);
        // console.log('endTime', endTime);
        const days = [];
        let currentTime = startTime;
        while(currentTime < endTime) {
            days.push(currentTime);
            currentTime += 86400;
        }
        // console.log('times', times);
        return days;
    }

    static addPercent(inputData = [], percent = 10, $max = 0, min = 0) {
        let max = $max;
        for (let i = 0, l = inputData.length; i < l; i += 1) {
            if (util.isDefined(inputData[i]) && util.isDefined(inputData[i].y) && inputData[i].y > max) {
                max = inputData[i].y;
            }
        }
        const data = [];
        for (let i = 0, l = inputData.length; i < l; i += 1) {
            if (util.isDefined(inputData[i]) && util.isDefined(inputData[i].y)) {
                const val = inputData[i].y;
                let newVal = val + (percent * max / 100);
                if (newVal < min) {
                    newVal = min;
                }
                data.push({
                    x: inputData[i].x,
                    y: val === null ? val : newVal,
                });
            }
        }
        return data;
    }

    static multiply(inputData = [], multiplicator = 11) {
        const data = [];
        for (let i = 0, l = inputData.length; i < l; i += 1) {
            if (util.isDefined(inputData[i]) && util.isDefined(inputData[i].y)) {
                const val = inputData[i].y;
                let newVal = val * multiplicator;
                data.push({
                    x: inputData[i].x,
                    y: val === null ? val : newVal,
                });
            }
        }
        return data;
    }

    static calcData(inputData = [], fn = () => {}, opts = {}) {
        const data = [];
        for (let i = 0, l = inputData.length; i < l; i += 1) {
            let newVal = 0;
            if (util.isDefined(inputData[i]) && util.isDefined(inputData[i].y)) {
                const val = inputData[i].y;
                newVal = fn(val, i, opts);
            }
            data.push({
                x: inputData[i].x,
                y: newVal,
            });
        }
        return data;
    }

    static calcProductionData(windspeed, idx, opts) {
        if (windspeed === null) {
            return null;
        }
        const MAX_EFFICIENCY = 12;
        const CUT_OFF_WS = 23;
        const maxEffect = Math.pow(12, 3);
        let ws = windspeed
        if (windspeed > CUT_OFF_WS) {
            ws = 0;
        } else if (windspeed > MAX_EFFICIENCY) {
            ws = MAX_EFFICIENCY;
        }
        const result = Math.round(Math.pow(ws, 3));

        if (util.isDefined(opts.min) && util.isDefined(opts.max)) {
            return Math.round(WifetUtil.normalizeRange(result, 0, maxEffect, opts.min, opts.max));
        }
        return Math.round(result);
    }

    static calcProductionDataNOK(Mw, idx, opts) {
        if (Mw === null) {
            return null;
        }
        if (util.isDefined(opts.priceData[idx]) && util.isDefined(opts.priceData[idx].y)) {
            const result = Math.round(Mw * opts.priceData[idx].y);
            return result;
        }
        return 0;
    }

    static getEpoch(inputDate) {
        let d;
        if (inputDate) {
            d = new Date(inputDate);
        } else {
            d = new Date();
        }
        return Math.floor(d.getTime() / 1000);
    }

    static getEpochMidnight(inputDate) {
        let d;
        if (inputDate) {
            d = new Date(inputDate);
        } else {
            d = new Date();
        }
        d.setHours(0, 0, 0, 0);
        return Math.floor(d.getTime() / 1000);
    }
}

export default WifetUtil;
