import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';
import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class WeatherOpen extends StoreModel {
    constructor() {
        super('weatherYr', {
            namePlural: 'forcasts',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/weather-open/',
                    params: {},
                },
            },
        });
    }

    @observable forcast = {};

    @observable forcasts = [];

    getWindSpeeds(name) {
        // "data": {
        //     "instant": {
        //         "details": {
        //             "air_pressure_at_sea_level": 1008.1,
        //             "air_temperature": 14.8,
        //             "cloud_area_fraction": 92.9,
        //             "dew_point_temperature": 9.1,
        //             "relative_humidity": 68.5,
        //             "wind_from_direction": 309.3,
        //             "wind_speed": 3.4
        const now = wutil.getEpoch();
        const windspeeds = this.forcasts.filter(e => e.dt > now).filter((e) => {
            const eName = e.name;
            // console.log(name, eName);
            return eName === name;
        }).map((e) => ({
            x: e.dt,
            y: util.getNestedValue(e, 'wind_speed'),
        }));
        // console.log('windspeeds', windspeeds);
        return windspeeds;
    }
}

const store = new WeatherOpen();
export default store;


