import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';

import style from './style.css';

const initialState = {
	searchText: '',
};

@observer
class Leftmenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    toggleMenu = (e) => {
        e.preventDefault();
        const { appState } = this.props.stores;
        appState.toggleMenu();
    };

	search = () => {
		const { searchText } = this.state;
		route(`/search/${searchText}`, true);
	}

    render() {
        const { appState } = this.props.stores;
        const { darkmode, menu, currentUrl } = appState;

		return (
			<nav class={`navbar navbar-inverse navbar-fixed-top ${style['sidebar-wrapper']} ${darkmode ? style.darkmode : ''} ${menu ? style['sidebar-wrapper-visible'] : ''}`}>
                <ul class={`nav ${style['sidebar-nav']}`}>
                    <li class='mb-2'>
                        <a class='nav-link' href='#' onClick={this.toggleMenu}><i class={`fas ${menu ? 'fa-long-arrow-alt-left' : 'fa-long-arrow-alt-right'}`} /></a>
                    </li>

                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/$/) ? style['sidebar-nav-active'] : ''}`} href='/'>
                            <i class='fab fa-hubspot mr-3' /> Home
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/weather/) ? style['sidebar-nav-active'] : ''}`} href='/weather'>
                            <i class='fas fa-cloud-sun-rain mr-3' /> Weather
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/demand/) ? style['sidebar-nav-active'] : ''}`} href='/demand'>
                            <i class='fas fa-hand-holding-usd mr-3' /> Demand
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/prices/) ? style['sidebar-nav-active'] : ''}`} href='/prices'>
                            <i class='fas fa-chart-line mr-3' /> Electricity prices
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/production/) ? style['sidebar-nav-active'] : ''}`} href='/production'>
                            <i class='fas fa-chart-bar mr-3' /> Production
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/wearstatus/) ? style['sidebar-nav-active'] : ''}`} href='/wearstatus'>
                            <i class='fas fa-battery-three-quarters mr-3' /> Wear status
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/wear$/) ? style['sidebar-nav-active'] : ''}`} href='/wear'>
                            <i class='fas fa-thermometer mr-3' /> Wear
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/strategy/) ? style['sidebar-nav-active'] : ''}`} href='/strategy'>
                            <i class='fas fa-chess-knight mr-3' /> Strategy
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/parkmanagement/) ? style['sidebar-nav-active'] : ''}`} href='/parkmanagement'>
                            <i class='fas fa-cog mr-3' /> Park Management
                        </a>
                    </li>
                    <li>
                        <a class={`nav-link ${currentUrl.match(/^\/bid/) ? style['sidebar-nav-active'] : ''}`} href='/bid'>
                            <i class='fas fa-money-bill-alt mr-3' /> Bid
                        </a>
                    </li>

                </ul>
            </nav>
		);
	}
}

export default Leftmenu;
