import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';

import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class WeatherAi extends StoreModel {
    constructor() {
        super('weatherAi', {
            namePlural: 'forcasts',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/weather-ai/',
                    params: {},
                },
            },
        });
    }

    @observable forcast = {};
   
    @observable forcasts = [];

    @action
    generateData(windData) {
        this.forcasts = wutil.makeRandomData({ cnt: 335, min: 0, max: 30, onlyUp: false, inputData: windData, diff: 1, maxSkew: 1 });
    }

    @computed
    get forcastsFiltered() {
        const now = wutil.getEpoch();
        return this.forcasts.filter(e => e.x > now);
    }
}

const store = new WeatherAi();
export default store;
