import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';

import wutil from '../lib/wifet-util';

configure({ enforceActions: 'always' });

class Price extends StoreModel {
    constructor() {
        super('price', {
            namePlural: 'prices',
            sort: 'title',
            limit: 100,
            api: {
                load: {
                    url: '/api/demand/',
                    params: {},
                },
            },
        });
    }

    @observable prices = [];

    @observable pricesAi = [];

    @action
    generateData() {
        this.prices = wutil.makeRandomData({ cnt: 335, min: 50, max: 200, onlyUp: false, inputData: [], diff: 5 });
        this.pricesAi = wutil.makeRandomData({ cnt: 335, min: 0, max: 200, onlyUp: false, inputData: this.prices, diff: 10 });
    }

    @computed
    get pricesAiFiltered() {
        const now = wutil.getEpoch();
        return this.pricesAi.filter(e => e.x > now);
    }
}

const store = new Price();
export default store;
